import React from "react";
import { useState, useEffect } from "react";
import './Home.css'
import photo from '../assets/me.jpeg';
export default function Home() {
    const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const toRotate = [
    "Graphic Designer",
    "Full Stack Developer",
    "Software Developer",
    "Student",
    "Game developer"
  ];
  const [text, setText] = useState("");
  const [delta, setDelta] = useState(100 - Math.random() * 50);
  const period = 2000;
  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => {
      clearInterval(ticker);
    };
  }, [text]);

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting
      ? fullText.substring(0, text.length - 1)
      : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(75);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setDelta(period);
    } else if (isDeleting && updatedText === "") {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setDelta(100);
    }
  };
    return (
        <div className="home" id="home">
            <div className="container">
                <div className="col left">
                    <h1>Hi,<br/>I'm Bo</h1>
                    <div className="roles">{text}</div>
                    <a href="#contact"><button className="button contacter">Contact me ✉️
                    <div class="button__horizontal"></div>
	<div class="button__vertical"></div></button></a>
                </div>
                <div className="col right">
                    <img className="hero" src={photo} alt="" />
                    <div className="contacts">
                    <ul>
                        <li className="contact-icon"><a href="https://github.com/bubb4rd"><box-icon type='logo' name='github' size={"30px"}></box-icon></a></li>
                        <li className="contact-icon"><a href="https://www.linkedin.com/in/bo-hubbard-4988b2246/"><box-icon name='linkedin' type='logo' size={"30px"}></box-icon></a></li>
                    </ul>
                    </div>
                </div>
            </div>
        </div>
    );
}